.product-card {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 16px;
  max-width: 300px;
  margin: 10px auto;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  font-family: Arial, sans-serif;
  background-color: #fff;
}

.product-card h3 {
  margin: 0 0 8px 0;
  color: #333;
  font-size: 1.25rem;
}

.product-card .price {
  margin: 0 0 8px 0;
  font-weight: bold;
  color: #0070f3;
}

.product-card .description {
  margin: 0;
  color: #555;
  font-style: italic;
}