.product-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  max-width: 500px;
  margin: auto;
}

.product-form label {
  font-weight: bold;
  margin-bottom: 0.3rem;
}

.product-form input,
.product-form textarea {
  padding: 0.5rem;
  font-size: 1rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 100%;
}

.product-form .error {
  color: red;
  font-size: 0.85rem;
  margin-top: -0.5rem;
}

.product-form .success {
  color: green;
  font-size: 0.95rem;
  font-weight: 500;
}

.product-form .info-msg {
  text-align: center;
  font-size: 1rem;
  margin-top: 1rem;
}

.product-form button {
  padding: 0.6rem 1rem;
  font-size: 1rem;
  background-color: #2b72ff;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.product-form button:disabled {
  background-color: #aaa;
  cursor: not-allowed;
}
