.products-container {
    padding: 24px;
    font-family: Arial, sans-serif;
}

.products-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 16px;
}

.products-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
}